@import url('https://fonts.googleapis.com/css2?family=Newsreader:ital,wght@0,400;0,700;1,400&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  font-family: 'Newsreader', serif;
  background-color: #F6F5F1;
  color: #000;
}

.App {
  min-height: 100vh;
  background-color: #F6F5F1;
  position: relative;
  margin: 0;
  padding: 0;
}

.App::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 30vh;
  background: linear-gradient(to bottom, #1EFF00, rgba(30, 255, 0, 0));
  z-index: 1;
}


.container {
  margin: 0 auto;
  padding: 0;
  position: relative;
  width: 100%;
}

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px;
  font-size: 16px;
  font-family: 'Newsreader', serif;
  position: relative;
  z-index: 2;
}

.nav-left {
  display: flex;
}

.nav-name {
  font-family: 'Newsreader', serif;
  font-size: 16px;
  color: #000;
  font-weight: 400;
}

.nav-right {
  display: flex;
  gap: 30px;
  position: relative;
}

.menu-toggle {
  display: none;
  cursor: pointer;
  font-size: 20px;
  font-weight: 400;
  color: #000;
  padding: 5px;
}

.nav-links {
  display: flex;
  gap: 30px;
}

/* Mobile Menu Inline */
.mobile-menu-inline {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.mobile-menu-links {
  display: flex;
  flex-direction: row;
  gap: 30px;
  align-items: center;
}

.mobile-nav-link {
  text-decoration: none;
  color: #000;
  font-size: 18px;
  font-family: 'Newsreader', serif;
  font-weight: 400;
  transition: opacity 0.2s ease;
}

.mobile-nav-link:hover {
  opacity: 0.7;
}

.mobile-nav-link.underlined {
  text-decoration: underline;
}

.mobile-nav-link.active {
  font-weight: 700 !important;
  font-family: 'Newsreader', serif;
}

.mobile-menu {
  position: fixed;
  inset: 0; /* top:0; right:0; bottom:0; left:0; */
  background: linear-gradient(to bottom, #1EFF00, rgba(30, 255, 0, 0)); /* restore gradient */
  z-index: 9999;
  display: flex;
  justify-content: center; /* vertical centering of the content container */
  align-items: center;     /* vertical centering */
  padding: 0;
}

.mobile-menu-content {
  width: 100%;
  max-width: 1200px;
  padding: 0 40px; /* left padding to left-align links visually */
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* left-align the links inside the centered container */
}

.mobile-menu-links {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.mobile-nav-link {
  text-decoration: underline;
  color: #000;
  font-size: 28px;
  font-family: 'Newsreader', serif;
  font-weight: 400;
  transition: opacity 0.2s ease;
  text-decoration-skip-ink: none;
  text-underline-offset: 0.1em;
  text-decoration-thickness: 1px;
  margin: 12px 0;
  display: block;
  width: 100%;
  text-align: left; /* ensure left alignment for the link text */
}

.mobile-author-description {
  display: none; /* hide author description in the full-screen mobile nav */
}

/* Close button inside the mobile overlay */
.mobile-close {
  position: absolute;
  top: 18px;
  right: 18px;
  font-size: 28px;
  line-height: 1;
  cursor: pointer;
  color: #000;
  background: transparent;
  border: none;
  z-index: 10000;
  padding: 6px 8px;
}

.mobile-close:active,
.mobile-close:focus {
  outline: none;
}


.nav-link {
  text-decoration: underline;
  color: #000;
  font-weight: 400;
  transition: opacity 0.2s ease;
  text-decoration-skip-ink: none;
  text-underline-offset: 0.1em;
  text-decoration-thickness: 1px;
}

.nav-link:hover {
  opacity: 0.7;
}

.nav-link.active {
  font-weight: 700 !important;
  font-family: 'Newsreader', serif;
}

.main-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 90vh;
  position: relative;
  z-index: 2;
}

.projects-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1;
  width: 100%;
}

.content-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 40px;
  text-align: left;
}

.project-item {
  font-size: 46px;
  font-family: 'Newsreader', serif;
  font-weight: 400;
  line-height: 0.8;
  margin: 20px 0;
  color: #000;
  position: relative;
  z-index: 100;
  letter-spacing: 0.02em;
}


/* Project links */
.project-link {
  text-decoration: none;
  color: #000;
  position: relative;
}

.highlight {
  text-decoration: underline;
  color: #000;
  cursor: pointer;
  transition: opacity 0.2s ease;
  text-decoration-skip-ink: none;
  text-underline-offset: 0.1em;
  text-decoration-thickness: 2px;
}

.highlight:hover {
  opacity: 0.7;
}

.author-description {
  font-family: 'Newsreader', serif;
  font-size: 16px;
  color: #000;
  font-weight: 400;
  margin-top: 30px;
  text-align: left;
  align-self: flex-start;
}


/* Responsive design */
@media (max-width: 768px) {
  .project-item {
    font-size: 48px;
  }
  
  .navigation {
    font-size: 16px;
  }
  
  .nav-right {
    gap: 20px;
  }
}

@media (max-width: 480px) {
  .main-content {
    text-align: left;
    align-items: flex-start;
  }
  
  .project-item {
    font-size: 28px;
    margin: 15px 0;
    line-height: 1.2;
  }
  
  .navigation {
    font-size: 16px;
  }
  
  .nav-right {
    gap: 15px;
  }
  
  .menu-toggle {
    display: block;
  }
  
  .nav-links {
    display: none;
  }
  
  .author-description {
    font-size: 16px;
    text-align: left;
  }
  
  .highlight:hover {
    opacity: 0.7;
  }
}

@media (min-width: 481px) {
  .mobile-menu-inline {
    display: none;
  }
}
